/* –°–ë–†–û–° */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #fff;
  color: #333;
}

a {
  text-decoration: none;
  color: #003366;
}

/* –û–ë–Å–†–¢–ö–ê */
.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* –•–ï–î–ï–† */
.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background-color: #e4ecf5;
  padding: 1rem;
}

.logo-block {
  flex: 1;
}

.nav {
  flex-wrap: wrap;
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  align-items: center;
}

/* –û–°–ù–û–í–ù–û–ô –ë–õ–û–ö */
.main {
  display: flex;
  flex: 1;
  min-height: 70vh;
}

.left, .right {
  width: 20%;
  background-color: #f4f4f4;
  padding: 1rem;
}

.center {
  width: 60%;
  background-color: #fff;
  padding: 2rem;
}

/* –§–£–¢–ï–† */
.footer {
  background-color: #d0dfea;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
}

/* –ë–∞–∑–æ–≤–∞—è —Å–µ—Ç–∫–∞ ‚Äî 3 –∫–æ–ª–æ–Ω–∫–∏ */
.main {
  display: flex;
  flex: 1;
  min-height: 70vh;
}

.left, .right {
  width: 20%;
  background-color: #f4f4f4;
  padding: 1rem;
}

.center {
  width: 60%;
  padding: 2rem;
  background-color: #fff;
}

/* ‚úÖ MEDIA QUERIES –¥–ª—è –∞–¥–∞–ø—Ç–∞—Ü–∏–∏ */

/* üíª –î–µ—Å–∫—Ç–æ–ø 1200px+ */
@media screen and (min-width: 1200px) {
  .main {
    flex-direction: row;
  }

  .left, .center, .right {
    width: auto;
  }
}

/* üì± –ú–æ–±–∏–ª—å–Ω—ã–µ —ç–∫—Ä–∞–Ω—ã –¥–æ 800px */
@media screen and (max-width: 800px) {
  .main {
    flex-direction: column;
  }

  .left, .center, .right {
    width: 100%;
    padding: 1rem;
  }

  .nav {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }

  .nav a {
    display: block;
    margin: 5px 0;
  }

  .header {
    flex-direction: column;
    align-items: flex-start;
  }
}
